import React from 'react';
import { Switch, Route, useRouteMatch, Link, Redirect } from 'react-router-dom';
import { Result } from 'antd';
// import AccountView from '../components/account/AccountView';
import AccountLayout from '../layout/AccountLayout';
import UpdateInfoContainer from '../containers/UpdateInfoContainer';
import ChangePasswordContainer from '../containers/ChangePasswordContainer';
import UploadAvatarContainer from '../containers/UploadAvatarContainer';

const AccountScene = () => {
  const { path } = useRouteMatch();
  /* hin th biu  s trn thng thua khi nh vi ngi,
   vi my, thng tin v s trn  u, tng s gi chi */

  return (
    <AccountLayout>
      <div style={{ padding: 24, overflow: 'initial' }}>
        <Switch>
          <Route exact path={path}>
            {/* <AccountView /> */}
            <Redirect to={`${path}/edit/profiles`} />
          </Route>
          <Route exact path={`${path}/edit/profiles`}>
            <UpdateInfoContainer />
          </Route>
          <Route exact path={`${path}/edit/password`}>
            <ChangePasswordContainer />
          </Route>
          <Route exact path={`${path}/edit/avatar`}>
            <UploadAvatarContainer />
          </Route>

          <Route path='*'>
            <Result
              status='404'
              title='404'
              subTitle='Sorry, the page you visited does not exist.'
              extra={<Link to='/'>Back Home</Link>}
            />
          </Route>
        </Switch>
      </div>
    </AccountLayout>
  );
};

export default AccountScene;
